:root {
  --rbl-text-focused: var(--color-gs00);
  --rbl-radio-unfocused: var(--color-zeta); }

.kai-rbl {
  width: auto;
  min-height: var(--list-item-height);
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow: hidden;
  outline: none; }
  .kai-rbl:focus {
    color: var(--rbl-text-focused); }
  .kai-rbl-button {
    margin: 0 1rem; }
    .kai-rbl-button-input-focused {
      color: var(--rbl-text-focused); }
    .kai-rbl-button-input-unfocused {
      color: var(--rbl-radio-unfocused); }
    .kai-rbl-button > input[type='radio'] {
      -webkit-appearance: none;
      -moz-appearance: none;
      position: relative;
      width: 2rem;
      height: 2rem;
      border-radius: 35%; }
    .kai-rbl-button > input[type='radio']::before {
      font-family: "kai-icons";
      position: absolute;
      font-size: 2rem; }
    .kai-rbl-button > input[type='radio']:not(:checked)::before {
      content: ""; }
    .kai-rbl-button > input[type='radio']:checked::before {
      content: ""; }
  .kai-rbl-line {
    font-family: 'Roboto';
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    width: 100%; }
    .kai-rbl-line span,
    .kai-rbl-line label {
      padding: 0;
      width: 100%;
      overflow: hidden; }
  .kai-rbl .left {
    box-sizing: border-box;
    padding: 1rem 0 1rem 1rem; }
  .kai-rbl .right {
    box-sizing: border-box;
    padding: 1rem 1rem 1rem 0; }
