/* ----------------------------------
 * Font
 * ---------------------------------- */
@import url(http://fonts.googleapis.com/css?family=Open+Sans);
h1,
.h1 {
  /** Text for Header, Light
     ---------------------------------------------------------*/
  font-weight: 300;
  font-size: 1.7rem; }

h2,
.h2 {
  /** Header of body texts, Semi-Bold
     ---------------------------------------------------------*/
  font-weight: 600;
  font-size: 1.7rem; }

h3,
.h3 {
  /** Text for Tabs, Regular
     ---------------------------------------------------------*/
  font-weight: 400;
  font-size: 1.4rem; }

h4,
.h4 {
  /** Text for Separators, Regular
     ---------------------------------------------------------*/
  font-weight: 400;
  font-size: 1.4rem; }

h5, .kai-softkey > *:first-child, .kai-softkey > *:last-child,
.h5 {
  /** Text for Software keys, Regular
     ---------------------------------------------------------*/
  font-weight: 600;
  font-size: 1.4rem; }

:root:not([lang|='fr']) h1,
:root:not([lang|='fr']) .h1,
:root:not([lang|='fr']) h3,
:root:not([lang|='fr']) .h3,
:root:not([lang|='fr']) h4,
:root:not([lang|='fr']) .h4,
:root:not([lang|='fr']) h5,
:root:not([lang|='fr']) .kai-softkey > *:first-child,
:root:not([lang|='fr']) .kai-softkey > *:last-child,
:root:not([lang|='fr']) .h5 {
  text-transform: capitalize; }

.p-pri {
  /** Primary information, Regular
     ---------------------------------------------------------*/
  font-weight: 400;
  font-size: 1.7rem; }

.p-sec {
  /** Secondary information, Regular
     ---------------------------------------------------------*/
  font-weight: 400;
  font-size: 1.4rem; }

.p-thi {
  /** Tertiary information, Regular
     ---------------------------------------------------------*/
  font-weight: 400;
  font-size: 1.2rem; }

p,
.p,
ul,
.ul {
  /** Body texts, Regular
     ---------------------------------------------------------*/
  font-weight: 400;
  font-size: 1.7rem; }

.p-link {
  /** Text for hyperlink, Bold, Underline
     ---------------------------------------------------------*/
  font-weight: 700;
  font-size: 1.7rem;
  text-decoration: underline; }

.p-btn {
  /** Text for Button, Regular, Capitalize
     ---------------------------------------------------------*/
  font-weight: 400;
  font-size: 1.7rem;
  text-transform: capitalize; }

/* ----------------------------------
   * Font for large text mode
   * ---------------------------------- */
.large-text h1,
.large-text .h1 {
  /** Text for Header, Light for large text mode
     ---------------------------------------------------------*/
  font-size: 2.2rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.large-text h2,
.large-text .h2 {
  /** Header of body texts, Semi-Bold for large text mode
     ---------------------------------------------------------*/
  font-size: 2.2rem; }

.large-text h3,
.large-text .h3 {
  /** Text for Tabs, Regular for large text mode
     ---------------------------------------------------------*/
  font-size: 1.8rem; }

.large-text h4,
.large-text .h4 {
  /** Text for Separators, Regular for large text mode
     ---------------------------------------------------------*/
  font-size: 1.8rem; }

.large-text h5, .large-text .kai-softkey > *:first-child, .large-text .kai-softkey > *:last-child,
.large-text .h5 {
  /** Text for Software keys, Regular for large text mode
     ---------------------------------------------------------*/
  font-size: 1.8rem; }

.large-text .p-pri {
  /** Primary information, Regular for large text mode
     ---------------------------------------------------------*/
  font-size: 2.2rem; }

.large-text .p-sec {
  /** Secondary information, Regular for large text mode
     ---------------------------------------------------------*/
  font-size: 1.8rem; }

.large-text .p-thi {
  /** Thirdary information, Regular for large text mode
     ---------------------------------------------------------*/
  font-size: 1.6rem; }

.large-text p,
.large-text .p,
.large-text ul,
.large-text .ul {
  /** Body texts, Regular for large text mode
     ---------------------------------------------------------*/
  font-size: 2.2rem; }

.large-text .p-link {
  /** Text for hyperlink, Bold, Underline for large text mode
     ---------------------------------------------------------*/
  font-size: 2.2rem; }

.large-text .p-btn {
  /** Text for Button, Regular for large text mode
     ---------------------------------------------------------*/
  font-size: 2.2rem; }

.icon-font {
  font-size: 3.2rem; }

.button-icon-font, .kai-softkey-btn span::before {
  font-size: 2.2rem; }

.arrow-font {
  font-size: 2rem; }

/* Set default font to all elements */
* {
  font-family: 'Open Sans';
  font-size: 10px;
  margin: 0;
  padding: 0; }

input {
  font-variant-numeric: normal; }

/*
  Gaia theme variables
  */
:root {
  /** Grey Colors
     ---------------------------------------------------------*/
  --color-alpha: #333333;
  --color-beta: #ffffff;
  --color-gamma: #4d4d4d;
  --color-delta: #5f5f5f;
  --color-epsilon: #858585;
  --color-zeta: #a6a6a6;
  --color-eta: #c7c7c7;
  --color-theta: #e7e7e7;
  --color-iota: #f4f4f4;
  --color-white: #ffffff;
  /** Brand Colors
   ---------------------------------------------------------*/
  --color-darkblue: #00539f;
  --color-blue: #00caf2;
  --color-turquoise: #27c8c2;
  --color-darkorange: #e66000;
  --color-orange: #ff9500;
  --color-yellow: #ffcb00;
  --color-violet: #c40c84;
  --color-carrotorange: #eb9423;
  --color-wathetblue: #005da5;
  /* gold */
  --color-gold: #ffa000;
  --color-gold-pressed: #cc8000;
  --color-warning: #fbbd3c;
  --color-destructive: #e2443a;
  --color-preferred: #00ba91;
  /** KaiOS Colors
     ---------------------------------------------------------*/
  /* Grey Scale Colors */
  --color-gs100: #000000;
  --color-gs90: #323232;
  --color-gs70: #6a6a6a;
  --color-gs45: #8c8c8c;
  --color-gs20: #cccccc;
  --color-gs10: #e6e6e6;
  --color-gs00: #ffffff;
  /* Primary Colors - Best for Highlighting selected UI element */
  --color-purple: #873eff;
  --color-pink: #f6529b;
  --color-lime: #82c51b;
  --color-cyan: #1cc1c4;
  --color-blue: #0da1e1;
  --color-orange: #ff8329;
  --color-red: #f3203d;
  /* Mid-dark Colors - Generally used for the press effect */
  --color-purple-mid-dark: #6c32cc;
  --color-pink-mid-dark: #bf2392;
  --color-lime-mid-dark: #6b9a23;
  --color-cyan-mid-dark: #31979e;
  --color-blue-mid-dark: #0a80b4;
  --color-orange-mid-dark: #cc5310;
  --color-red-mid-dark: #c21930;
  /* Dark Colors - Generally used for the header background */
  --color-purple-dark: #320374;
  --color-pink-dark: #49122c;
  --color-lime-dark: #143a06;
  --color-cyan-dark: #03757c;
  --color-blue-dark: #21407a;
  --color-orange-dark: #391e0a;
  --color-red-dark: #5c0600;
  /** Background
     ---------------------------------------------------------*/
  --background: var(--color-beta);
  --background-plus: var(--color-theta);
  --background-minus: var(--color-zeta);
  /** Text Color
     ---------------------------------------------------------*/
  --text-color: var(--color-gamma);
  --text-color-minus: var(--color-eta);
  /** Button
     ---------------------------------------------------------*/
  --button-background: var(--color-iota);
  /** Links
     ---------------------------------------------------------*/
  --link-color: var(--highlight-color);
  /** Inputs
     ---------------------------------------------------------*/
  --input-background: var(--color-beta);
  --input-color: var(--color-alpha);
  --input-clear-background: #909ca7;
  /** Buttons
     ---------------------------------------------------------*/
  --button-box-shadow: inset 0 -1px 0 #d8d8d8, inset 0 1px 0 #eeeeee;
  --button-box-shadow-active: var(--button-box-shadow);
  --color-purple-pressed: var(--color-purple-mid-dark);
  --color-pink-pressed: var(--color-pink-mid-dark);
  --color-lime-pressed: var(--color-lime-mid-dark);
  --color-cyan-pressed: var(--color-cyan-mid-dark);
  --color-blue-pressed: var(--color-blue-mid-dark);
  --color-orange-pressed: var(--color-orange-mid-dark);
  --color-red-pressed: var(--color-red-mid-dark);
  /** Header
     ---------------------------------------------------------*/
  --header-purple-background: var(--color-purple-dark);
  --header-pink-background: var(--color-pink-dark);
  --header-lime-background: var(--color-lime-dark);
  --header-cyan-background: var(--color-cyan-dark);
  --header-blue-background: var(--color-blue-dark);
  --header-orange-background: var(--color-orange-dark);
  --header-red-background: var(--color-red-dark);
  --header-disabled-button-color: rgba(255, 255, 255, 0.3);
  /** Statusbar, Softkeybar and ClientHeight
     *  Should update to proper value according to device size
     ---------------------------------------------------------*/
  --statusbar-height: 2.6rem;
  --softkeybar-height: 3rem;
  --tabs-height: 3rem;
  --header-height: 2.8rem;
  --header-large-text-height: 2.4rem;
  --tabbar-height: 2.7rem;
  --list-item-height: 6rem;
  --progress-bar-height: 6rem;
  --slider-height: 6rem;
  --separator-height: 2.4rem;
  --button-height: 3.6rem;
  --rocketbar-urlbar-height: 3.6rem;
  --statusbar-softkeybar-height: 5.6rem;
  --header-tabbar-height: 5.5rem;
  --client-height: calc(100% - var(--statusbar-softkeybar-height)); }

.theme-system,
.theme-launcher {
  /** Brand Color
     ---------------------------------------------------------*/
  --highlight-color: var(--color-purple);
  --highlight-color-pressed: var(--color-purple-pressed);
  /** Header
     ---------------------------------------------------------*/
  --header-background: var(--header-purple-background);
  --header-color: var(--color-beta); }

:root {
  --statusbar-height: 2.6rem;
  --softkeybar-height: 3rem;
  --header-height: 2.8rem; }

ul {
  margin: 0;
  padding: 0; }

.kai-softkey-btn {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden; }

@keyframes loading {
  from {
    left: -50px;
    width: 30%; }
  50% {
    width: 30%; }
  70% {
    width: 70%; }
  80% {
    left: 50%; }
  95% {
    left: 120%; }
  to {
    left: 100%; } }

:root {
  --softkeybar-background-color: var(--color-gs20, #cccccc);
  --softkey-color: var(--color-gs90, #323232);
  --softkey-color--icon: var(--color-gs70, #6a6a6a);
  --softkey-color--disabled: var(--color-eta, #c7c7c7);
  --softkey-background-color--active: #00aacc;
  --softkey-icon-size: 2rem; }

.kai-softkey {
  z-index: var(--z-index--softkey, 9999);
  display: flex;
  justify-content: space-between;
  outline: none;
  text-align: center;
  color: var(--softkey-color);
  background-color: var(--softkeybar-background-color);
  position: absolute;
  bottom: 0;
  width: 100%; }
  .kai-softkey:not(.visible) {
    opacity: 0; }
  .kai-softkey > * {
    text-transform: capitalize; }
    .kai-softkey > *:first-child {
      min-width: 7.7rem;
      padding: 0 0 0 0.5rem;
      text-align: left; }
    .kai-softkey > *:nth-child(2) {
      font-size: 1.6rem;
      font-weight: 700;
      min-width: 3rem;
      max-width: 7.6rem;
      text-transform: uppercase;
      padding: 0 0.5rem; }
    .kai-softkey > *:last-child {
      min-width: 7.7rem;
      text-align: right;
      padding: 0 0.5rem 0 0; }
  .kai-softkey-btn {
    display: block;
    min-width: 1rem;
    height: var(--softkeybar-height);
    line-height: var(--softkeybar-height);
    border: unset;
    overflow: hidden;
    color: var(--softkey-color);
    background: unset; }
    .kai-softkey-btn:active, .kai-softkey-btn.active {
      background-color: var(--softkey-background-color--active); }
    .kai-softkey-btn[disabled] {
      --softkey-color: var(--softkey-color--disabled);
      background-color: transparent;
      pointer-events: auto; }
    .kai-softkey-btn[data-icon] {
      --softkey-font-size: var(--softkey-icon-size);
      --softkey-color: var(--softkey-color--icon); }
    .kai-softkey-btn span::before {
      line-height: var(--softkeybar-height);
      color: var(--softkey-color); }
